'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { useRouter, useSearchParams } from 'next/navigation';
import { toast } from 'sonner';
import { useAuth } from '@/hooks/useAuth';
import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';
import Link from 'next/link'; 
import { 
    Calendar as CalendarIcon, 
    Clock, 
    User, 
    CreditCard, 
    Image as ImageIcon,
    ArrowRight,
    ArrowLeft,
    Loader2
} from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Calendar } from '@/components/ui/calendar'; 
import {
    Popover,
    PopoverContent,
    PopoverTrigger,
} from "@/components/ui/popover";
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import { cn } from '@/lib/utils';

// Types
import { Booking, PaymentMethod } from '@/types';

export default function BookingWizardPage() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const { user } = useAuth({ middleware: 'auth' });

    // --- STATE UTAMA ---
    const [step, setStep] = useState(1);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [bookedDates, setBookedDates] = useState<Date[]>([]); 

    // --- STATE DATA FORM ---
    const [formData, setFormData] = useState({
        // Step 1
        nama_lengkap: '',
        email: '',
        nomor_telepon: '',
        nama_club: '', 
        
        // Step 2
        tanggal_booking: undefined as Date | undefined,
        jam_mulai: '',
        durasi_jam: '1',
        
        // Step 3
        payment_method_id: '',
        asal_bank: '',      
        nama_pengirim: '',  
        jumlah_dp: '',
        bukti_pembayaran: null as File | null,
    });

    // --- DATA MASTER ---
    const [paymentMethods, setPaymentMethods] = useState<PaymentMethod[]>([]);
    
    // State untuk Lapangan
    const [lapanganId, setLapanganId] = useState<number | null>(null); 
    const [hargaPerJam, setHargaPerJam] = useState(0); 

    // 1. Fetch Data Awal
    useEffect(() => {
        const initData = async () => {
            if (user) {
                setFormData(prev => ({
                    ...prev,
                    nama_lengkap: user.name,
                    email: user.email,
                    nomor_telepon: user.nomor_telepon || '',
                    nama_pengirim: user.name // Default nama pengirim
                }));
            }

            try {
                // Fetch Lapangan
                const resLap = await axios.get('/api/lapangans');
                if (resLap.data.length > 0) {
                    const lap = resLap.data[0];
                    setLapanganId(lap.id);
                    setHargaPerJam(Number(lap.harga_per_jam));
                }

                // Fetch Payment Methods
                const resPay = await axios.get('/api/paymentMethods');
                setPaymentMethods(resPay.data.filter((p: any) => p.is_aktif));

                // Fetch Bookings
                const resBook = await axios.get('/api/bookings');
                const dates = resBook.data.map((b: Booking) => new Date(b.tanggal_booking));
                setBookedDates(dates);

            } catch (error: any) {
                console.error("Gagal memuat data:", error);
                toast.error("Gagal memuat data booking.");
            }
        };
        initData();

        const dateParam = searchParams.get('date');
        if (dateParam) {
            setFormData(prev => ({ ...prev, tanggal_booking: new Date(dateParam) }));
            setStep(2);
        }
    }, [user, searchParams]);


    // --- LOGIKA HELPER ---
    const timeSlots = Array.from({ length: 13 }, (_, i) => {
        const hour = i + 11; 
        return `${hour.toString().padStart(2, '0')}:00`;
    });

    const getJamSelesai = () => {
        if (!formData.jam_mulai) return '--:--';
        const [hours, minutes] = formData.jam_mulai.split(':').map(Number);
        const endHours = hours + Number(formData.durasi_jam);
        return `${String(endHours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
    };

    const totalHarga = Number(formData.durasi_jam) * hargaPerJam;

    // --- NAVIGASI STEP ---
    const nextStep = () => {
        if (step === 1) {
            if (!formData.nama_club) return toast.error("Mohon isi Nama Club!");
        }
        if (step === 2) {
            if (!formData.tanggal_booking || !formData.jam_mulai) return toast.error("Pilih jadwal main!");
            if (!lapanganId) return toast.error("Data lapangan belum dimuat. Refresh halaman.");
        }
        setStep(prev => prev + 1);
        window.scrollTo(0, 0); 
    };

    const prevStep = () => setStep(prev => prev - 1);

    // --- SUBMIT FINAL (PERBAIKAN) ---
    const handleSubmit = async () => {
        if (!formData.payment_method_id || !formData.bukti_pembayaran || !formData.asal_bank || !formData.nama_pengirim) {
            return toast.error("Lengkapi data pembayaran & upload bukti!");
        }

        if (!lapanganId) {
            return toast.error("ID Lapangan tidak valid.");
        }

        setIsSubmitting(true);
        try {
            // 1. Siapkan Data Booking (PERBAIKAN DI SINI)
            const bookingPayload = {
                lapangan_id: lapanganId,
                tanggal_booking: format(formData.tanggal_booking!, 'yyyy-MM-dd'),
                jam_mulai: formData.jam_mulai,
                durasi_jam: parseInt(formData.durasi_jam), 
                payment_method_id: parseInt(formData.payment_method_id),
                
                // --- Mapping Field ke Database Baru ---
                acara: formData.nama_club,           // Ke kolom 'acara'
                asal_bank: formData.asal_bank,       // Ke kolom 'asal_bank' (BARU)
                nama_pengirim: formData.nama_pengirim // Ke kolom 'nama_pengirim' (BARU)
                // Catatan sudah dihapus, jadi tidak perlu dikirim
            };

            // 2. Create Booking (Header)
            const resBooking = await axios.post('/api/bookings', bookingPayload);
            const bookingId = resBooking.data.id;

            // 3. Upload Pembayaran (Detail)
            const paymentData = new FormData();
            paymentData.append('jumlah_dp', formData.jumlah_dp || totalHarga.toString());
            paymentData.append('bukti_pembayaran', formData.bukti_pembayaran!);

            await axios.post(`/api/bookings/${bookingId}/payment`, paymentData);

            toast.success("Booking Berhasil!", { description: "Silakan tunggu konfirmasi admin." });
            router.push('/pelanggan/home');

        } catch (error: any) {
            console.error("Error Submit:", error.response?.data);
            
            const responseData = error.response?.data;
            const status = error.response?.status;

            if (status === 422) {
                if (responseData.errors?.jam_mulai) {
                    toast.error("Jadwal Bentrok!", { description: responseData.errors.jam_mulai[0] });
                    setStep(2); 
                } else {
                    toast.error("Validasi Gagal", { description: responseData.message || "Cek inputan Anda." });
                }
            } else {
                toast.error(responseData?.message || "Gagal memproses booking.");
            }
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="min-h-screen bg-white font-sans">

            <div className="max-w-6xl mx-auto px-4 md:px-8 py-12">
                <div className="flex flex-col lg:flex-row gap-16 mt-[100px]">
                    
                    {/* --- BAGIAN KIRI: STEPPER --- */}
                    <div className="w-full lg:w-1/4 space-y-10">
                        <div>
                            <h2 className="text-3xl font-bold text-[#D93F21]">Step {step}</h2>
                            <p className="text-gray-600 text-sm leading-relaxed">
                                {step === 1 && "masukan data diri anda"}
                                {step === 2 && "pilih jadwal pesanan anda"}
                                {step === 3 && "pilih metode pembayaran anda"}
                            </p>
                        </div>

                        <div className="relative space-y-8">
                            <div className="absolute left-[22px] top-2 bottom-2 w-0.5 bg-gray-200 -z-10"></div>

                            {/* Step Items */}
                            {[
                                { id: 1, label: "Data Diri", icon: User },
                                { id: 2, label: "Pilih Jadwal", icon: CalendarIcon },
                                { id: 3, label: "Pembayaran", icon: CreditCard },
                            ].map((item) => (
                                <div key={item.id} className="flex items-center gap-4 bg-white">
                                    <div className={`
                                        w-12 h-12 rounded-full flex items-center justify-center text-white shadow-lg transition-all duration-300
                                        ${step >= item.id ? 'bg-[#D93F21]' : 'bg-gray-300'}
                                    `}>
                                        <item.icon className="w-5 h-5" />
                                    </div>
                                    <span className={`font-medium text-lg ${step === item.id ? 'text-gray-900' : 'text-gray-400'}`}>
                                        {item.label}
                                    </span>
                                </div>
                            ))}
                        </div>
                    </div>

                    {/* --- BAGIAN KANAN: FORM --- */}
                    <div className="flex-1">
                        <div className="border-l-2 border-[#D93F21] pl-8 md:pl-12 py-2 mb-10">
                            <h3 className="text-3xl font-bold text-[#D93F21] mb-2">
                                {step === 1 && "Data Diri"}
                                {step === 2 && "Pilih Jadwal"}
                                {step === 3 && "Pembayaran"}
                            </h3>
                            <p className="text-gray-500 text-sm">
                                {step === 1 && "masukan data diri anda dan lanjutkan ke langkah selanjutnya"}
                                {step === 2 && "pilih jadwal anda anda dan lanjutkan ke langkah selanjutnya"}
                                {step === 3 && "lakukan pembayaran dan masukan bukti pembayaran"}
                            </p>
                        </div>

                        <div className="space-y-6 max-w-2xl animate-in slide-in-from-right-4 duration-500">
                            
                            {/* === STEP 1 === */}
                            {step === 1 && (
                                <>
                                    <div className="space-y-2">
                                        <Label>Nama Lengkap</Label>
                                        <Input value={formData.nama_lengkap} disabled className="bg-gray-100 h-12 border-gray-200 rounded-lg" />
                                    </div>
                                    <div className="space-y-2">
                                        <Label>Nomor Telepon</Label>
                                        <Input value={formData.nomor_telepon} disabled className="bg-gray-100 h-12 border-gray-200 rounded-lg" />
                                    </div>
                                    <div className="space-y-2">
                                        <Label>Email</Label>
                                        <Input value={formData.email} disabled className="bg-gray-100 h-12 border-gray-200 rounded-lg" />
                                    </div>
                                    <div className="space-y-2">
                                        <Label>Nama Club (Wajib)</Label>
                                        <Input 
                                            placeholder="masukan nama club anda" 
                                            className="h-12 border-gray-300 focus:border-[#D93F21] focus:ring-[#D93F21] rounded-lg"
                                            value={formData.nama_club}
                                            onChange={e => setFormData({...formData, nama_club: e.target.value})}
                                        />
                                    </div>
                                </>
                            )}

                            {/* === STEP 2 === */}
                            {step === 2 && (
                                <>
                                    <div className="space-y-2">
                                        <Label>Tanggal Main</Label>
                                        <Popover>
                                            <PopoverTrigger asChild>
                                                <Button
                                                    variant={"outline"}
                                                    className={cn(
                                                        "w-full justify-start text-left font-normal border-gray-300 bg-gray-50 h-12 rounded-lg",
                                                        !formData.tanggal_booking && "text-muted-foreground"
                                                    )}
                                                >
                                                    <CalendarIcon className="mr-2 h-4 w-4" />
                                                    {formData.tanggal_booking ? format(formData.tanggal_booking, "PPP", { locale: localeId }) : <span>Pilih tanggal main</span>}
                                                </Button>
                                            </PopoverTrigger>
                                            <PopoverContent className="w-auto p-0 bg-white rounded-xl shadow-2xl border-none" align="start">
                                                <div className="p-4 bg-white rounded-xl">
                                                    <Calendar
                                                        mode="single"
                                                        selected={formData.tanggal_booking}
                                                        onSelect={(date) => setFormData({...formData, tanggal_booking: date})}
                                                        initialFocus
                                                        disabled={(date) => date < new Date(new Date().setHours(0, 0, 0, 0))}
                                                        modifiers={{
                                                            booked: bookedDates
                                                        }}
                                                        modifiersStyles={{
                                                            booked: { color: '#D93F21', fontWeight: 'bold', textDecoration: 'underline' }
                                                        }}
                                                        className="rounded-md border-none"
                                                        classNames={{
                                                            head_cell: "text-gray-500 rounded-md w-10 font-normal text-[0.8rem] pb-4 pt-2",
                                                            row: "flex w-full mt-2",
                                                            cell: "h-10 w-10 text-center text-sm p-0 relative [&:has([aria-selected])]:bg-accent first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
                                                            day: "h-10 w-10 p-0 font-normal aria-selected:opacity-100 hover:bg-gray-100 rounded-lg transition-all",
                                                            day_selected: "bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white focus:bg-[#D93F21] focus:text-white shadow-md",
                                                            day_today: "bg-gray-100 text-gray-900 font-bold",
                                                            day_outside: "text-gray-300 opacity-50",
                                                            day_disabled: "text-gray-300 opacity-50",
                                                        }}
                                                    />
                                                </div>
                                            </PopoverContent>
                                        </Popover>
                                    </div>

                                    {/* SLOT WAKTU GRID */}
                                    <div className="space-y-3">
                                        <Label>Pilih Jam Mulai</Label>
                                        <div className="grid grid-cols-4 sm:grid-cols-5 gap-3">
                                            {timeSlots.map((time) => (
                                                <button
                                                    key={time}
                                                    type="button"
                                                    onClick={() => setFormData({...formData, jam_mulai: time})}
                                                    className={`
                                                        py-2 px-3 rounded-lg border text-sm font-medium transition-all
                                                        ${formData.jam_mulai === time 
                                                            ? 'bg-[#D93F21] border-[#D93F21] text-white shadow-md' 
                                                            : 'bg-white border-gray-200 text-gray-700 hover:border-[#D93F21] hover:text-[#D93F21]'
                                                        }
                                                    `}
                                                >
                                                    {time}
                                                </button>
                                            ))}
                                        </div>
                                    </div>

                                    <div className="grid grid-cols-2 gap-6">
                                        <div className="space-y-2">
                                            <Label>Jam Mulai</Label>
                                            <div className="flex items-center px-4 border border-gray-200 bg-gray-100 h-12 rounded-lg text-gray-800 font-medium">
                                                {formData.jam_mulai || '--:--'}
                                            </div>
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Jam Akhir (Otomatis)</Label>
                                            <div className="flex items-center px-4 border border-gray-200 bg-gray-100 h-12 rounded-lg text-gray-500">
                                                {getJamSelesai()}
                                            </div>
                                        </div>
                                    </div>

                                    <div className="space-y-2">
                                        <Label>Total Durasi (Jam)</Label>
                                        <Input 
                                            type="number" 
                                            min="1" 
                                            max="5"
                                            className="h-12 bg-gray-50 border-gray-300 rounded-lg"
                                            value={formData.durasi_jam}
                                            onChange={e => setFormData({...formData, durasi_jam: e.target.value})}
                                        />
                                    </div>
                                </>
                            )}

                            {/* === STEP 3 === */}
                            {step === 3 && (
                                <>
                                    <div className="grid grid-cols-2 gap-6">
                                        <div className="space-y-2">
                                            <Label>Jenis Pembayaran</Label>
                                            <Select onValueChange={(v) => setFormData({...formData, payment_method_id: v})}>
                                                <SelectTrigger className="h-12 bg-gray-50 border-gray-300 rounded-lg"><SelectValue placeholder="Pilih Bank" /></SelectTrigger>
                                                <SelectContent>
                                                    {paymentMethods.map(method => (
                                                        <SelectItem key={method.id} value={method.id.toString()}>{method.nama_metode}</SelectItem>
                                                    ))}
                                                </SelectContent>
                                            </Select>
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Asal Bank</Label>
                                            <Input placeholder="pilih asal bank" className="h-12 bg-gray-50 border-gray-300 rounded-lg" value={formData.asal_bank} onChange={e => setFormData({...formData, asal_bank: e.target.value})} />
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Status Pembayaran</Label>
                                            <div className="flex items-center px-4 border border-gray-200 bg-gray-100 h-12 rounded-lg text-gray-500 text-sm">Menunggu Konfirmasi</div>
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Nama Pengirim</Label>
                                            <Input placeholder="masukan nama pengirim" className="h-12 bg-gray-50 border-gray-300 rounded-lg" value={formData.nama_pengirim} onChange={e => setFormData({...formData, nama_pengirim: e.target.value})} />
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Jumlah Pembayaran</Label>
                                            <Input type="number" placeholder="masukan jumlah pembayaran" className="h-12 bg-gray-50 border-gray-300 rounded-lg" value={formData.jumlah_dp} onChange={e => setFormData({...formData, jumlah_dp: e.target.value})} />
                                            <p className="text-xs text-[#D93F21] font-medium mt-1">Total Tagihan: Rp {new Intl.NumberFormat('id-ID').format(totalHarga)}</p>
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Bukti Pembayaran</Label>
                                            <div className="relative">
                                                <Input type="file" accept="image/*" className="cursor-pointer h-12 bg-gray-50 border-gray-300 rounded-lg pt-2.5" onChange={e => setFormData({...formData, bukti_pembayaran: e.target.files?.[0] || null})} />
                                                <ImageIcon className="absolute right-3 top-3.5 text-gray-400 w-5 h-5 pointer-events-none" />
                                            </div>
                                        </div>
                                    </div>

                                    <div className="mt-4 p-4 bg-gray-50 rounded-xl border border-gray-200 text-sm shadow-sm">
                                        <p className="font-bold text-gray-800 mb-1">Informasi Rekening Tujuan Transfer</p>
                                        <p className="text-gray-600">BCA No Rekening: <span className="font-mono font-bold text-black">327-143-0051</span></p>
                                        <p className="text-gray-600">A/N: <span className="font-bold text-black">Muhammad Hatta Mustafa</span></p>
                                    </div>
                                </>
                            )}

                            {/* === ACTION BUTTONS === */}
                            <div className="flex gap-4 mt-12 justify-end pt-6 border-t border-gray-100">
                                {/* Tombol Cancel / Back */}
                                {step > 1 ? (
                                    <Button 
                                        variant="outline" 
                                        onClick={prevStep}
                                        className="bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white border-none rounded-full px-8 h-12 text-base shadow-md"
                                    >
                                        <ArrowLeft className="w-4 h-4 mr-2" /> Kembali
                                    </Button>
                                ) : (
                                    <Link href="/pelanggan/home">
                                        <Button 
                                            variant="outline" 
                                            className="bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white border-none rounded-full px-8 h-12 text-base shadow-md"
                                        >
                                            Cancel
                                        </Button>
                                    </Link>
                                )}

                                {/* Tombol Next / Submit */}
                                {step < 3 ? (
                                    <Button 
                                        onClick={nextStep}
                                        className="bg-[#D93F21] hover:bg-[#b9351b] text-white rounded-full px-10 h-12 text-base font-bold shadow-lg hover:shadow-xl transition-all"
                                    >
                                        Next <ArrowRight className="w-4 h-4 ml-2" />
                                    </Button>
                                ) : (
                                    <Button 
                                        onClick={handleSubmit}
                                        disabled={isSubmitting}
                                        className="bg-green-600 hover:bg-green-700 text-white rounded-full px-10 h-12 text-base font-bold shadow-lg w-48 hover:shadow-xl transition-all"
                                    >
                                        {isSubmitting ? <Loader2 className="animate-spin mr-2" /> : "Booking Now"}
                                    </Button>
                                )}
                            </div>

                        </div>
                    </div>

                </div>
            </div>
        </div>
    );
}